.main105{
    display: flex;
    justify-content: space-between;
}
.preview105{
    width: 70%;
    text-align: justify;
    margin-bottom: 20px;
    margin: 20px;
    filter: drop-shadow(10px 0px 10px  rgb(192, 142, 68));
   
}
.personalinfo105{
    display: flex;
    justify-content: space-between;
    margin-bottom:10px;
    background-color: aqua;
    padding: 10px;
}
.imagediv105{
    width: 120px;
    height: 120px;
    border-radius: 50%;
}
.imagediv105 img{
    width: 120px;
    height: 120px;
}
.namediv105{
    text-align: center;
    margin-right: 2px;
    margin-top:30px;
    padding: 10px;
}
.contactdiv105 p{
    margin-bottom: -12px;
    padding: 5px;
}
.namediv105 h2{
    margin-bottom: -15px;
}
.inner105-1{
    text-align: end;
    margin-bottom: 10px;
    margin-right: px;
}
.education-section105 p{
    margin-bottom: -5px;
}
