    margin-bottom: -5px;
}
.technical-skills-title105 li{
    margin-bottom: -5px;
}
.line-container {
    display: flex;
    width: 50%;
    height: 5px;    /* Set the height of the line */
    margin-left: 150px;
  }
  
  .filled-line {
    background-color: #ffc150;  /* Gold color for the filled part */
    height: 100%;               /* Fill the height */
  }
  
  .empty-line {
    background-color: grey;     /* Grey color for the unfilled part */
    height: 100%;               /* Fill the height */
  }
  .divider105{
    width: 7px;
    height: inherit;
    background-color: #131212;
    color: grey; 
  }
  .resume-download-section{
    width: 30%;
